import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DefaultData } from 'src/app/default-data/default-data';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {

  constructor(private fb: FormBuilder,
    private currentRoute: ActivatedRoute,
    private router: Router) { }

  user: any;
  userList: Array<any> = [];
  userId: number = -1;

  ngOnInit(): void {

    let id = this.currentRoute.snapshot.paramMap.get("id");
    if (id != null) {
      this.userId = Number.parseInt(id);
    }

    let user = localStorage.getItem('user');
    if (user != null) {
      this.userList = JSON.parse(user);

      let data = this.userList.filter(c => c.id == this.userId);

      if (data == null || data.length == 0) {

        this.router.navigate(['user']);
      }

      this.user = {
        id: data[0].id,
        name: data[0].userName,
        password: data[0].password,
        role: new DefaultData().getRoleName(data[0].role)
      }
    }
  }

}
