import { Component, EventEmitter, OnInit, Output, } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { DefaultData } from 'src/app/default-data/default-data';
import { SearchService } from 'src/app/services/search.service';
import { DwLoginComponent } from '../dialogs/dw-login/dw-login.component';

export interface DialogData {
  username: string;
  password: string;
}
interface Language {
  value: string;
  viewValue: string;
}

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  isLogin: boolean = false;

  username: string = "";
  password: string = "";
  userList: Array<any> = [];
  userId: number = -1

  searchInput: string = "";

  constructor(public dialog: MatDialog,
    private router: Router,
    private currentRoute: ActivatedRoute,
    private searchService: SearchService) { }

  ngOnInit() {

    this.isLogin = false;

    if (localStorage.getItem("IS_LOGIN")) {
      this.isLogin = true;
    }
  }

  loginDialog(): void {
    const dialogRef = this.dialog.open(DwLoginComponent, {
      width: '250px',
      data: { username: this.username, password: this.password }
    });

    dialogRef.afterClosed().subscribe(result => {

      if (result) {

        let user = localStorage.getItem("user");
        if (user != null) {

          this.userList = JSON.parse(user);

          let data = this.userList.filter(c => c.userName === result.username && c.password === result.password);

          if (data.length == 1) {

            console.log("WELCOME");

            let userInfo = {
              userId: data[0].id,
              username: data[0].userName,
              role: data[0].role,
              roleName: new DefaultData().getRoleName(data[0].role)
            }

            this.isLogin = true;
            localStorage.setItem("VISIBLE_CABINET", JSON.stringify(true));
            localStorage.setItem("IS_LOGIN", JSON.stringify(userInfo));
            this.router.navigate([]);
            window.location.reload();
          }

          this.router.navigate(['/']);
        }
      }

    });
  }

  account() {
    localStorage.setItem("VISIBLE_CABINET", JSON.stringify(true));
    this.router.navigate([]);
    window.location.reload();
  }

  logout() {
    localStorage.removeItem("VISIBLE_CABINET");
    localStorage.removeItem("IS_LOGIN");
    this.router.navigate([]);
    window.location.reload();
  }

  search(f: NgForm) {

    this.searchService.changeMessage({ type: 'announcement', filter: f.value.searchInput });
  }

}
