import { Component, OnInit } from '@angular/core';
import { DefaultData } from './default-data/default-data';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'I-Shopping';
  isLogin: boolean = false;
  isVisible: boolean = false;

  constructor() { }

  ngOnInit() {

    new DefaultData().loadDefaultData();

    let login = localStorage.getItem("IS_LOGIN");
    let visibleCabinet = localStorage.getItem('VISIBLE_CABINET');
    if (login != null && visibleCabinet != null) {
      this.isLogin = true;
      this.isVisible = true;
    } else {
      this.isLogin = false;
      this.isVisible = false;
    }
  }
}
