import { Component, AfterViewInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { DefaultData } from 'src/app/default-data/default-data';

export interface User {
  id: string;
  name: string;
  password: string;
  role: string;
}

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements AfterViewInit {

  displayedColumns: string[] = ['id', 'name', 'role', 'actions'];
  dataSource: MatTableDataSource<User>;

  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  userList: User[] = this.formatList();
  allUsers: Array<any> = this.getAllUsers();

  constructor(private router: Router) {
    this.dataSource = new MatTableDataSource(this.userList);
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  // filter
  applyFilter(event: Event) {

    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

  }

  // profile
  profile(row: any) {
    this.router.navigate(['user/profile', row.id]);
  }

  // edit
  edit(row: any) {
    this.router.navigate(['user/edit', row.id]);
  }

  // delete
  delete(row: any) {
    let index = this.allUsers.findIndex(c => c.id == row.id);

    if (index > -1) {

      this.allUsers.splice(index, 1);

      localStorage.setItem("user", JSON.stringify(this.allUsers));

      // buni o'zgartirish kerak !
      document.location.reload();
    }
  }

  // add
  add() {
    this.router.navigate(['user/create']);
  }

  formatList(): User[] {
    let array: User[] = [];

    let user = localStorage.getItem("user");

    if (user != null) {

      let list = JSON.parse(user);

      for (let i = 0; i < list.length; i++) {

        array.push({
          id: list[i].id,
          name: list[i].userName,
          password: list[i].password,
          role: list[i].role,
        })
      }
    }

    return array;
  }

  getAllUsers() {
    let user = localStorage.getItem('user');
    if (user != null) {
      return JSON.parse(user);
    }
    return [];
  }

  getRoleName(type: number) {
    return new DefaultData().getRoleName(type);
  }

}
